<!--  -->
<template>
  <div class="layout">
    <ul class="tags clearfix">
      <router-link tag="li" to="/Home" class="active">
        
        <span>></span>
      </router-link>
      <li></li>
    </ul>
    <div class="content clearfix">
      <!--  -->
      <div class="shareList">
        <h3></h3>
        <ul>
          <li>
            <img src="@/assets/image/info/WeChatpage.png" alt />
          </li>
        </ul>
      </div>
      <!--  -->
      <section class="a-section">
        <dt> -----------MORE VX</dt>
        <dd class="count middle">
          <img src="@/assets/image/info/tiemf.png" alt />
          <span>2019-4-29</span>
          <img src="@/assets/image/info/eye.png" alt />
          <span>165456</span>
        </dd>
        <div class="article">
          <img src="@/assets/image/info/read1.png" alt />
          <p>
             meta 
             routes  
            /foo/bar  URL 
             $route  ()  $route.matched  $route.matched  meta 
            
          </p>
        </div>
        <div class="i-tag middle">
          <label></label>
          <span v-for="item in labelList">{{item}}</span>
        </div>

        <div class="f-favour">
          <div class="activeFavour">
            <div class="a-wrap">
              <img src="@/assets/image/info/commend.png" alt />
              <p>123165</p>
            </div>
          </div>
          <div class="c-comment">
            <h3>
              
              <font>(54)</font>
            </h3>
            <div>
              <textarea name id maxlength="500" placeholder="..."></textarea>
              <div class="f-wrap clearfix">
                <span>500</span>
                <button></button>
              </div>
            </div>
          </div>

          <div class="hot-comment">
            <h3></h3>
            <ul class="user-comment clearfix">
              <li v-for="item,i in comment">
                <img class="user" :src="item.src" alt />
                <div class="right">
                  <p class="moreWord">
                    {{item.name}}
                    <span>{{item.time}}</span>
                  </p>
                  <p class="moreMWord" :title="item.title">{{item.title}}</p>
                  <div class="count middle">
                    <img src="@/assets/image/info/comment_up.png" alt />
                    <span>{{item.upnum}}</span>
                    <img src="@/assets/image/info/comment_downx.png" alt />
                    <span>{{item.downnum}}</span>
                  </div>
                </div>
              </li>
            </ul>
            <div class="bar-comment"></div>
          </div>
        </div>
      </section>
      <!--  -->
      <div class="recommend">
        <div class="top">
          <h3></h3>
          <ul>
            <li class="t-li clearfix" v-for="item in dataTop">
              <img :src="item.src" alt />
              <article>
                <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
                <dd class="count clearfix">
                  <span>{{item.time}}</span>
                  <span>{{item.see}}</span>
                </dd>
              </article>
            </li>
          </ul>
        </div>
        <div class="bottom">
          <h3></h3>
          <ul>
            <li class="b-li clearfix" v-for="item in dataBottom">
              <img :src="item.src" alt />
              <article>
                <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
                <dd>{{item.remark}}</dd>
                <dd class="star middle">
                  <font></font>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                </dd>
              </article>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
export default {
  //import
  components: {},
  data() {
    //
    return {
      labelList: ["", "", ""],
      comment: [
        {
          src: require("@/assets/image/userInfo.png"),
          name: "",
          time: "3",
          title:
            " props ",
          up: true,
          dowm: false,
          upnum: 123213,
          downnum: 2123
        },
        {
          src: require("@/assets/image/userInfo.png"),
          name: "",
          time: "3",
          title:
            " props ",
          up: true,
          dowm: false,
          upnum: 123213,
          downnum: 2123
        },
        {
          src: require("@/assets/image/userInfo.png"),
          name: "",
          time: "3",
          title:
            " props ",
          up: true,
          dowm: false,
          upnum: 123213,
          downnum: 2123
        }
      ],
      dataTop: [
        {
          src: require("@/assets/image/info/article1.png"),
          title:
            " Vue  router  router ",
          time: 3,
          see: 213213
        },
        {
          src: require("@/assets/image/info/article1.png"),
          title:
            " Vue  router  router ",
          time: 3,
          see: 213213
        }
      ],
      dataBottom: [
        {
          src: require("@/assets/image/b3.png"),
          title: "",
          remark: "",
          see: 3
        },
        {
          src: require("@/assets/image/b2.png"),
          title: "",
          remark: "",
          see: 4
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
$c9: #999;
$c6: #666;
$c3: #333;
$b: #2878ff;
$f4: #f4f6f8;
.layout {
  width: 1240px;
  margin: 0 auto;
  .tags {
    padding: 30px 0 50px 0;
    li {
      float: left;
      margin-right: 5px;
      color: $c6;
      cursor: pointer;
      font-size: 14px;
      line-height: 14px;
      &.active {
        color: $b;
      }
    }
  }
}
.content {
  .shareList {
    float: left;
    text-align: center;
    margin-top: 100px;
    margin-left: -108px;
    h3 {
      font-size: 14px;
      color: $c9;
      &::before {
        content: "";
        display: inline-block;
        height: 1px;
        width: 24px;
        background: $c9;
        vertical-align: middle;
        margin-right: 4px;
      }
      &::after {
        content: "";
        display: inline-block;
        height: 1px;
        width: 24px;
        background: $c9;
        vertical-align: middle;
        margin-left: 4px;
      }
    }
    ul {
      margin-top: 30px;
      li {
        cursor: pointer;
      }
    }
  }
  section.a-section {
    width: 736px;
    float: left;
    overflow: hidden;
    margin-right: 125px;
    & > dt {
      font-size: 30px;
      color: $c3;
      line-height: 30px;
    }
    & > .count {
      margin: 30px 0 20px 0;
      font-size: 0;
      img {
        vertical-align: middle;
        margin-right: 10px;
      }
      span {
        font-size: 14px;
        color: $c9;
        vertical-align: middle;
        line-height: 14px;
        &:nth-of-type(1) {
          margin-right: 50px;
        }
      }
    }
    div.article {
    }
    .i-tag {
      margin-top: 60px;
      margin-bottom: 70px;
      span {
        display: inline-block;
        border: 1px solid $f4;
        color: $c6;
        background: $f4;
        padding: 6px 10px;
        border-radius: 3px;
        font-size: 14px;
        margin-right: 10px;
      }
    }
  }

  .f-favour {
    .activeFavour {
      text-align: center;
      .a-wrap {
        width: 71px;
        display: inline-block;
        img {
          cursor: pointer;
        }
        p {
          font-size: 12px;
          line-height: 12px;
          color: #333;
          margin-top: 10px;
        }
      }
    }
    .c-comment {
      box-sizing: border-box;
      font-size: 0;
      h3 {
        font-size: 16px;
        margin-bottom: 20px;
        line-height: 16px;
        color: #333;
        margin-top: 70px;
      }
      & > div {
        border: 1px solid #ccc;
        box-sizing: border-box;
        textarea {
          width: 100%;
          height: 102px;
          border: none;
          padding: 10px 12px;
          box-sizing: border-box;
          color: #999;
          font-size: 14px;
          &::placeholder {
            color: #999;
          }
        }
        .f-wrap {
          background: #f4f6f8;
          width: 100%;
          height: 40px;
          line-height: 40px;
          box-sizing: border-box;
          span {
            float: left;
            font-size: 14px;
            color: #999;
            padding-left: 20px;
          }
          button {
            float: right;
            color: #fff;
            background: #2878ff;
            height: 40px;
            width: 108px;
            vertical-align: middle;
          }
        }
      }
    }
    .hot-comment {
      margin-top: 40px;
      margin-bottom: 70px;
      h3 {
        font-size: 16px;
        line-height: 16px;
        color: #333;
        margin-bottom: 20px;
      }
      .user-comment {
        & > li {
          margin-top: 40px;
          &:nth-of-type(1) {
            margin-top: 0;
          }
          .user {
            height: 50px;
            width: 50px;
            float: left;
            margin-left: 10px;
          }
          .right {
            margin-left: 76px;
            padding-top: 18px;
            & > p:nth-of-type(1) {
              font-size: 14px;
              font-weight: 600;
              line-height: 14px;
              color: #333;
              span {
                margin-left: 30px;
                color: #999;
                font-weight: 500;
              }
            }
            & > p:nth-of-type(2) {
              margin: 20px 0;
            }
            .count {
              font-size: 0;
              img {
                vertical-align: middle;
                margin-right: 10px;
                cursor: pointer;
              }
              span {
                font-size: 14px;
                color: #666;
                vertical-align: middle;
                line-height: 14px;
                &:nth-of-type(1) {
                  margin-right: 40px;
                }
              }
            }
          }
        }
      }
      .bar-comment {
        width: 100%;
        height: 50px;
        background: #f0f2f3;
        line-height: 50px;
        text-align: center;
        margin-top: 70px;
        font-size: 14px;
        color: #999;
        cursor: no-drop;
        &.active {
          cursor: pointer;
          color: #333;
        }
      }
    }
  }

  .recommend {
    float: left;
    width: 375px;
    h3 {
      font-size: 24px;
      line-height: 24px;
      color: #333;
      margin-bottom: 30px;
    }

    .top {
      ul {
        padding-bottom: 50px;
        border-bottom: 1px solid #eceff2;
        .t-li {
          margin-bottom: 30px;
          height: 90px;
          cursor: pointer;
          &:last-of-type {
            margin-bottom: 0;
          }
          img {
            float: left;
          }
          article {
            position: relative;
            margin-left: 144px;
            height: 100%;
            dt {
              font-size: 16px;
              color: #333;
              line-height: 22px;
            }
            .count {
              position: absolute;
              bottom: 0;
              width: 100%;
              span {
                font-size: 14px;
                color: #999;
                line-height: 14px;
                float: left;
                &:nth-of-type(2) {
                  float: right;
                }
              }
            }
          }
        }
      }
    }
    .bottom {
      margin-top: 50px;
      ul {
        .b-li {
          margin-bottom: 36px;
          height: 110px;
          cursor: pointer;
          &:last-of-type {
            margin-bottom: 0;
          }
          img {
            float: left;
            width: 110px;
            height: 110px;
            border: 1px solid #f4f6f8;
            box-sizing: border-box;
            border-radius: 10px;
          }
          article {
            margin-left: 140px;
            position: relative;
            height: 100%;
            dt {
              font-size: 18px;
              color: #333;
              line-height: 18px;
            }
            dd:nth-of-type(1) {
              margin: 12px 0 20px 0;
              font-size: 14px;
              color: #999;
            }
            .star {
              position: absolute;
              bottom: 0;
              width: 100%;
              font-size: 0;
              font {
                font-size: 14px;
                color: #999;
                vertical-align: middle;
              }
              span {
                display: inline-block;
                vertical-align: middle;
                margin-right: 10px;
                width: 15px;
                height: 15px;
                background: url("../../assets/image/notstar.png") no-repeat;
                &.full {
                  background: url("../../assets/image/stars.png") no-repeat;
                }
                &.half {
                  background: url("../../assets/image/halfstar.png") no-repeat;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>


