<!--  -->
<template>
  <div id="layout">
    <v-Headers @isMask="isMask"></v-Headers>
    <main>
      <router-view @isMask="isMask" ></router-view>
    </main>
    <v-footers></v-footers>
    <div class="m-mask" v-if="mask"></div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Headers from "@/components/common/headers.vue";
import Footers from "@/components/common/footers.vue";
export default {
  //import

  components: {
    "v-Headers": Headers,
    "v-footers": Footers
  },
  //provideinject
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    //
    return {
      mask: false,
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    isMask(flag) {
      // console.log(this.mask);
      this.mask = flag;
    },
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    // this.$router.afterEach((to, from, next) => {
    //   window.scrollTo(0, 0);
    // });
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
</style>


