<!--  -->
<template>
  <div class="layout">
    <ul class="tags clearfix">
      <router-link tag="li" to="/Home" class="active">
        
        <span>></span>
      </router-link>
      <li class="active">
        
        <span>></span>
      </li>
      <li></li>
    </ul>
    <div class="intro clearfix">
      <img src="@/assets/image/info/appc1.png" alt />
      <div class="right clearfix">
        <h3>
          
          <font>152</font>
        </h3>
        <p>,</p>
        <div class="share middle">
          <font></font>
          <img src="@/assets/image/info/WeChatpage.png" alt />
        </div>
      </div>
    </div>
    <div class="v-app">
      <ul class="u-ul clearfix" v-for="arr,i in List">
        <router-link tag="li" to="/MiniProgram" v-for="item,j in arr" :key="item.id">
          <div class="wrap clearfix">
            <img :src="item.src" alt />
            <div class="right">
              <h3>{{item.title}}</h3>
              <p class="moreMWord">{{item.at}}</p>
            </div>
          </div>
          <div class="i-tag">
            <span v-for="tag,i in item.tag">{{tag}}</span>
            <button class="activeInfo"></button>
          </div>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Headers from "@/components/common/headers.vue";
import Footers from "@/components/common/footers.vue";
export default {
  //import
  components: {
    "v-Headers": Headers,
    "v-footers": Footers
  },
  data() {
    //
    return {
      List: [
        {
          id: 1,
          src: require("@/assets/image/b1.png"),
          title: "",
          at: ",,",
          tag: ["", "", ""]
        },
        {
          id: 2,
          src: require("@/assets/image/b2.png"),
          title: "",
          at: ",,",
          tag: ["", "", ""]
        },
        {
          id: 3,
          src: require("@/assets/image/b3.png"),
          title: "",
          at: ",,",
          tag: ["", "", ""]
        },
        {
          id: 4,
          src: require("@/assets/image/b4.png"),
          title: "",
          at: ",,",
          tag: ["", "", ""]
        },
        {
          id: 5,
          src: require("@/assets/image/b5.png"),
          title: "",
          at: ",,",
          tag: ["", "", ""]
        },
        {
          id: 6,
          src: require("@/assets/image/b6.png"),
          title: "",
          at: ",,",
          tag: ["", "", ""]
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {
    var data = [];
    for (let i = 0; i < this.List.length; i++) {
      data.push(this.List.splice(0, 3));
    }
    this.List = data;
    console.log(this.List);
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
$c9: #999;
$c6: #666;
$c3: #333;
$b: #2878ff;
$f4: #f4f6f8;
.layout {
  width: 1240px;
  margin: 0 auto;
}
.tags {
  padding-top: 30px;
  li {
    float: left;
    margin-right: 5px;
    color: $c6;
    cursor: pointer;
    font-size: 14px;
    line-height: 14px;
    &.active {
      color: $b;
    }
  }
}
.intro {
  margin-top: 50px;
  & > img {
    height: 182px;
    width: 390px;
    float: left;
  }
  .right {
    margin-left: 420px;
    h3 {
      font-size: 24px;
      color: $c3;
      line-height: 24px;
      padding: 15px 0 20px 0;
      font {
        float: right;
        font-size: 14px;
        color: $c3;
      }
    }

    p {
      font-size: 14px;
      color: $c9;
      line-height: 14px;
    }
    .share {
      margin-top: 40px;
      font {
        font-size: 14px;
        color: $c9;
        display: inline-block;
        vertical-align: middle;
      }
      img {
        height: 44px;
        width: 44px;
        vertical-align: middle;
        cursor: pointer;
      }
    }
  }
}
.v-app {
  padding: 30px 0;
  .u-ul {
    padding: 40px 0;
    border-bottom: 1px solid #eceff2;
    &:last-of-type {
      border-bottom: 0;
    }
    li {
      margin-right: 115px;
      float: left;
      overflow: hidden;
      width: 336px;
      &:nth-child(3n) {
        margin-right: 0;
      }
      .wrap {
        & > img {
          height: 74px;
          width: 74px;
          border-radius: 10px;
          border: 1px solid $f4;
          box-sizing: border-box;
          float: left;
        }
        .right {
          margin-left: 90px;
          h3 {
            font-size: 18px;
            color: $c3;
            line-height: 18px;
            margin-bottom: 14px;
          }
          p {
            font-size: 14px;
            color: $c9;
            line-height: 14px;
          }
        }
      }
      .i-tag {
        margin-top: 20px;
        position: relative;
        span {
          display: inline-block;
          border: 1px solid $f4;
          color: $c6;
          background: $f4;
          padding: 4px 10px;
          border-radius: 3px;
          font-size: 12px;
          margin-right: 10px;
          &:last-of-type {
            margin-right: 0;
          }
        }
        .activeInfo {
          position: absolute;
          right: 0;
          border: 1px solid $c6;
          color: $c6;
          background: #fff;
          height: 26px;
          width: 68px;
          border-radius: 50px;
          vertical-align: middle;
          line-height: 24px;
          font-size: 12px;
          margin-right: 10px;
          &:hover {
            border: 1px solid $b;
            color: #fff;
            background: $b;
          }
          &:last-of-type {
            margin-right: 0;
          }
        }
      }
    }
  }
}
</style>


