<!--  -->
<template>
  <div class="layout">
    <div class="v-layout">
      <div class="c-header">
        <router-link tag="h4" to="/editInfo"></router-link>
        <router-link tag="h4" to="/editAccount" class="active"></router-link>
      </div>
      <div class="c-content">
        <ul>
          <li class="middle">
            <label></label>
            <span>17965326598</span>
            <button></button>
          </li>
          <li class="middle">
            <label></label>
            <span></span>
            <button></button>
          </li>
          <li class="middle">
            <label></label>
            <span></span>
            <button class="active"></button>
          </li>
          <li class="middle">
            <label>QQ</label>
            <span></span>
            <button></button>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {};
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
$c9: #999;
$c6: #666;
$c3: #333;
$b: #2878ff;
$f4: #f4f6f8;
.layout {
  background: #fafbfc;
  padding: 20px 0;
  .v-layout {
    width: 1240px;
    height: 940px;
    margin: 0 auto;
    box-shadow: 0 0 15px -3px #ddd;
    background: #fff;

    .c-header {
      height: 85px;
      line-height: 85px;
      border-bottom: 1px solid #f4f6f8;
      box-sizing: border-box;
      h4 {
        cursor: pointer;
        color: #999;
        font-size: 18px;
        margin-left: 46px;
        display: inline-block;
        &:nth-of-type(2) {
          margin-left: 100px;
        }
        &.active {
          color: #333;
          border-bottom: 1px solid #333;
        }
      }
    }

    .c-content {
      padding: 100px 150px;
      ul {
        li {
          font-size: 0;
          margin-bottom: 46px;
          position: relative;
          width: 340px;
          height: 32px;
          label {
            color: #666;
            text-align: right;
            width: 70px;
            font-size: 14px;
            display: inline-block;
            margin-right: 20px;
            vertical-align: middle;
          }
          span {
            font-size: 14px;
            vertical-align: middle;
            color: #333;
          }
          button {
            position: absolute;
            right: 0;
            border: 1px solid $b;
            color: #fff;
            background: $b;
            height: 32px;
            padding: 0 30px;
            border-radius: 3px;
            vertical-align: middle;
            font-size: 14px;
            line-height: 30px;
            cursor: pointer;
            &.active {
              color: $c6;
              border: 1px solid $c6;
              background: #fff;
            }
          }
        }
      }
    }
  }
}
</style>


