<!--  -->
<template>
  <div class="layout">
    <ul class="tags clearfix">
      <router-link tag="li" to="/Home" class="active">
        
        <span>></span>
      </router-link>
      <li></li>
    </ul>
    <!--  -->
    <div class="content clearfix">
      <section class="hot-article clearfix">
        <ul>
          <li class="t-li clearfix" v-for="item in dataTop">
            <img :src="item.src" alt />
            <article>
              <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
              <dd></dd>
              <dd class="count middle">
                <img src="@/assets/image/info/eye.png" alt />
                <span>20129</span>
                <div class="clearfix">
                  <img src="@/assets/image/info/letter.png" alt />
                  <span>1654</span>
                </div>
              </dd>
            </article>
          </li>
        </ul>
        <div class="bar-comment"></div>
      </section>
      <!--  -->
      <div class="recommend clearfix">
        <div class="bottom">
          <h3></h3>
          <ul>
            <li class="b-li clearfix" v-for="item in dataBottom">
              <img :src="item.src" alt />
              <article>
                <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
                <dd>{{item.remark}}</dd>
                <dd class="star middle">
                  <font></font>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                </dd>
              </article>
            </li>
          </ul>
        </div>
        <div class="top">
          <h3></h3>
          <ul>
            <li class="t-li clearfix" v-for="item in dataTop">
              <img :src="item.src" alt />
              <article>
                <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
                <dd class="count clearfix">
                  <span>{{item.time}}</span>
                  <span>{{item.see}}</span>
                </dd>
              </article>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
export default {
  //import
  components: {},
  data() {
    //
    return {
      labelList: ["", "", ""],
      comment: [
        {
          src: require("@/assets/image/userInfo.png"),
          name: "",
          time: "3",
          title:
            " props ",
          up: true,
          dowm: false,
          upnum: 123213,
          downnum: 2123
        },
        {
          src: require("@/assets/image/userInfo.png"),
          name: "",
          time: "3",
          title:
            " props ",
          up: true,
          dowm: false,
          upnum: 123213,
          downnum: 2123
        },
        {
          src: require("@/assets/image/userInfo.png"),
          name: "",
          time: "3",
          title:
            " props ",
          up: true,
          dowm: false,
          upnum: 123213,
          downnum: 2123
        }
      ],
      dataTop: [
        {
          src: require("@/assets/image/info/article1.png"),
          title:
            " Vue  router  router ",
          time: 3,
          see: 213213
        },
        {
          src: require("@/assets/image/info/article1.png"),
          title:
            " Vue  router  router ",
          time: 3,
          see: 213213
        }
      ],
      dataBottom: [
        {
          src: require("@/assets/image/b3.png"),
          title: "",
          remark: "",
          see: 3
        },
        {
          src: require("@/assets/image/b2.png"),
          title: "",
          remark: "",
          see: 4
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
$c9: #999;
$c6: #666;
$c3: #333;
$b: #2878ff;
$f4: #f4f6f8;
.layout {
  width: 1240px;
  margin: 0 auto;
  overflow: hidden;
  .tags {
    padding: 30px 0 50px 0;
    li {
      float: left;
      margin-right: 5px;
      color: $c6;
      cursor: pointer;
      font-size: 14px;
      line-height: 14px;
      &.active {
        color: $b;
      }
    }
  }
}
.content {
  padding-bottom: 100px;
}
.hot-article {
  width: 739px;
  float: left;
  overflow: hidden;
  margin-right: 125px;
  h3 {
    font-size: 18px;
    line-height: 18px;
    color: #333;
    margin-bottom: 30px;
    span {
      font-size: 14px;
      color: #666;
      float: right;
      cursor: pointer;
    }
  }
  ul {
    .t-li {
      margin-bottom: 30px;
      height: 140px;
      cursor: pointer;
      &:last-of-type {
        margin-bottom: 0;
      }
      & > img {
        float: left;
        width: 160px;
        height: 140px;
      }
      article {
        position: relative;
        margin-left: 180px;
        height: 100%;
        dt {
          font-size: 16px;
          color: #333;
          line-height: 22px;
        }
        & > dd:nth-of-type(1) {
          color: $c9;
          line-height: 14px;
          margin-top: 16px;
        }
        .count {
          position: absolute;
          bottom: 0;
          width: 100%;
          font-size: 0;
          img {
            vertical-align: middle;
            margin-right: 10px;
          }
          span {
            font-size: 14px;
            color: #999;
            line-height: 14px;
            vertical-align: middle;
          }
          & > div {
            float: right;
          }
        }
      }
    }
  }
  .bar-comment {
    width: 100%;
    height: 50px;
    background: #f0f2f3;
    line-height: 50px;
    text-align: center;
    margin-top: 70px;
    font-size: 14px;
    color: #999;
    cursor: no-drop;
    &.active {
      cursor: pointer;
      color: #333;
    }
  }
}

.recommend {
  float: left;
  width: 375px;
  h3 {
    font-size: 24px;
    line-height: 24px;
    color: #333;
    margin-bottom: 30px;
  }

  .top {
    margin-top: 50px;
    ul {
      .t-li {
        margin-bottom: 30px;
        height: 90px;
        cursor: pointer;
        &:last-of-type {
          margin-bottom: 0;
        }
        img {
          float: left;
        }
        article {
          position: relative;
          margin-left: 144px;
          height: 100%;
          dt {
            font-size: 16px;
            color: #333;
            line-height: 22px;
          }
          .count {
            position: absolute;
            bottom: 0;
            width: 100%;
            span {
              font-size: 14px;
              color: #999;
              line-height: 14px;
              float: left;
              &:nth-of-type(2) {
                float: right;
              }
            }
          }
        }
      }
    }
  }
  .bottom {
    ul {
      padding-bottom: 50px;
      border-bottom: 1px solid #eceff2;
      .b-li {
        margin-bottom: 36px;
        height: 110px;
        cursor: pointer;
        &:last-of-type {
          margin-bottom: 0;
        }
        img {
          float: left;
          width: 110px;
          height: 110px;
          border: 1px solid #f4f6f8;
          box-sizing: border-box;
          border-radius: 10px;
        }
        article {
          margin-left: 140px;
          position: relative;
          height: 100%;
          dt {
            font-size: 18px;
            color: #333;
            line-height: 18px;
          }
          dd:nth-of-type(1) {
            margin: 12px 0 20px 0;
            font-size: 14px;
            color: #999;
          }
          .star {
            position: absolute;
            bottom: 0;
            width: 100%;
            font-size: 0;
            font {
              font-size: 14px;
              color: #999;
              vertical-align: middle;
            }
            span {
              display: inline-block;
              vertical-align: middle;
              margin-right: 10px;
              width: 15px;
              height: 15px;
              background: url("../../assets/image/notstar.png") no-repeat;
              &.full {
                background: url("../../assets/image/stars.png") no-repeat;
              }
              &.half {
                background: url("../../assets/image/halfstar.png") no-repeat;
              }
            }
          }
        }
      }
    }
  }
}
</style>


